<template>
    <gmap-map ref="mapRef" 
    :center="myCurrentLocation"
    :zoom="13"
    map-type-id="roadmap"
    style="width:100%; height:100%;"
    disableDefaultUI="true"
    :options="{
      disabledDefaultUi: true,
      mapTypeControl: false,
      scaleControl: false,
      streetViewControl: false,
      rotateControl: false,
      fullscreenControl: false,
      zoomControl: false,
    }"
    > 
        <gmap-marker :position="myCurrentLocation">
            <v-img src="../../assets/login-hero.png" style="z-index: 60;"/>
        </gmap-marker>  
    </gmap-map>
  </template>
  
  <script>
  import { gmapApi } from 'vue2-google-maps'
  
  export default {
    data(){
      return{
        myCurrentLocation: {
          lat: 0,
          lng: 0
        }
      }
    },
    created(){
      this.getCurrentLocation()
    },
    methods:{
      getCurrentLocation(){
        if(navigator.geolocation){
          navigator.geolocation.getCurrentPosition(position => {
            this.myCurrentLocation = {
                address: 'Tu ubicación',
                lat: position.coords.latitude,
                lng: position.coords.longitude
            }
          })
        } else {
          console.log("Geolocation is not suported by this browser")
        }
      },
      get Google(){
        return gmapApi
      },
      drawLineBetweenEndPoints(){
        let google = this.Google()
  
        let directionService = new google.maps.DirectionsService()
        let directionDisplay = new google.maps.DirectionsRenderer()
  
        let travel = {
          origin: this.myCurrentLocation,
          destination: this.destination,
          travelMode: 'DRIVING'
        }
  
        directionService.route(travel, (res, status) => {
          if(status === 'OK'){
            let map = this.$refs.mapRef
  
            directionDisplay = new google.maps.DirectionsRenderer({
              polylineOptions:{
                strokeColor: "#2ab4a6",
                stropeOpacity: 0.8,
                strokeWeight: 5,
              }
            })
            this.setTravelDistance(res.routes[0].legs[0].distance.text)
            this.setTravelDuration(res.routes[0].legs[0].duration.text)
            directionDisplay.setMap(map.$mapObject)
            directionDisplay.setDirections(res)
          }
        })
      }
    }
  }
  </script>
  
